amily: var(--font-heading)\r\n\r\n                    .main\r\n                        color: var(--color-heading)\r\n\r\n                    .chevron, .part\r\n                        display: none\r\n\r\n                .toggle-open\r\n                    display: flex\r\n\r\n            &.open\r\n                .header-content\r\n                    .toggle-open\r\n                        .svg-wrapper .hamburger-path\r\n                            stroke-dashoffset: -23.8\r\n\r\n        .layout\r\n            margin-top: 3.5rem\r\n            min-height: 0\r\n\r\n            .menu\r\n                width: 280px\r\n                position: fixed\r\n                z-index: 1000\r\n                top: 3.5rem\r\n                right: 0\r\n                border-right: none\r\n                border-left: 1px solid #ffffff99\r\n                height: calc(100% -  3.5rem)\r\n                overflow-y: auto\r\n                transform: translateX(100%)\r\n\r\n                &.open\r\n                    transform: translateX(0)\r\n\r\n                .menu-item::before\r\n                    width: 100%\r\n                    border-radius: 0\r\n"],"sourceRoot":""}]);
