\n            transition: all 0.5s ease\r\n\r\n        .small-slide-enter-from\r\n            opacity: 0\r\n            transform: translate(0px, 0px) !important\r\n            \r\n        .small-slide-leave-to\r\n            opacity: 0\r\n            transform: translate(0px, 0px) !important\r\n\r\n        .small-slide-leave-active\r\n            position: absolute !important\r\n            top: 0 !important\r\n\r\n        .container\r\n            position: relative\r\n            z-index: 1\r\n            display: flex\r\n            align-items: center\r\n            overflow: hidden\r\n            border-radius: 1rem\r\n            background: var(--color-background)\r\n            box-shadow: var(--shadow-elevation-high)\r\n            margin-inline: 4rem\r\n\r\n            img\r\n                width: 250px\r\n                align-self: stretch\r\n                object-fit: cover\r\n\r\n            .text-wrapper\r\n                padding: 2rem\r\n                display: flex\r\n                flex-direction: column\r\n\r\n                h2\r\n                    font-size: 3rem\r\n                    font-weight: 700\r\n                    margin-block: 0rem 1rem\r\n\r\n                p\r\n                    margin: 0rem\r\n\r\n                a\r\n                    margin-top: 1.6rem\r\n                    margin-right: auto\r\n                    border-radius: .5rem\r\n                    padding: 0 2rem\r\n                    height: 2.5rem\r\n                    display: flex\r\n                    align-items: center\r\n                    background: var(--color-brand)\r\n                    color: var(--color-background)\r\n                    text-decoration: none\r\n\r\n    @media screen and (max-width: 900px)\r\n        .outer-wrapper\r\n            .container\r\n\r\n                img\r\n                    width: 210px\r\n\r\n                .text-wrapper\r\n                    h2\r\n                        font-size: 2rem\r\n\r\n    @media screen and (max-width: 700px)\r\n        .outer-wrapper\r\n            > img\r\n                transform: translateY(0)\r\n\r\n            .inner-wrapper\r\n                > button\r\n                    height: 3rem\r\n                    width: 3rem\r\n                    \r\n                    &.prev\r\n                        left: .5rem\r\n\r\n                    &.next\r\n                        right: .5rem\r\n\r\n            .container\r\n                flex-direction: column\r\n                margin-inline: 3rem\r\n\r\n                img\r\n                    width: 100%\r\n                    height: 300px\r\n                    object-fit: contain\r\n                    background: var(--color-background-soft)\r\n\r\n                .text-wrapper\r\n                    h2\r\n                        font-size: 1.75rem\r\n\r\n    @media screen and (max-width: 500px)\r\n        .outer-wrapper\r\n            > img\r\n                display: none\r\n\r\n            .inner-wrapper\r\n                padding-top: 4.5rem\r\n\r\n                > button\r\n                    height: 3rem\r\n                    width: 3rem\r\n                    top: unset\r\n                    top: 0\r\n                    transform: translateY(0)\r\n                    \r\n                    &.prev\r\n                        left: 0\r\n\r\n                    &.next\r\n                        right: 0\r\n                        \r\n            .container\r\n                flex-direction: column\r\n                margin-inline: 0rem\r\n                border-radius: .75rem\r\n                box-shadow: var(--shadow-elevation-medium)\r\n\r\n                img\r\n                    width: 100%\r\n                    height: 300px\r\n                    object-fit: contain\r\n                    background: var(--color-background-soft)\r\n\r\n                .text-wrapper\r\n                    h2\r\n                        font-size: 1.75rem\r\n"],"sourceRoot"